import React, { useEffect, useState, useMemo } from "react";
import { Card, Col, Row, Spin, Input, Tabs, Rate, Pagination } from "antd";
import { format } from "date-fns";
import { fetchMovies, fetchGenres, ALL_GENRES, fetchGuestSession, voteForMovie  } from "../api/tmdb";
import { debounce } from 'lodash';
import { GenresContext } from "./GenresContext";
import "./MovieList.css";

const { TabPane } = Tabs;

const MovieList = () => {
  const [movies, setMovies] = useState([]);
  const [genres, setGenres] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState("");
  const [ratedMovies, setRatedMovies] = useState({});
  const [activeTab, setActiveTab] = useState("search");
  const [currentPage, setCurrentPage] = useState(1);
  const [totalResults, setTotalResults] = useState(0);
  const [guestSessionId, setGuestSessionId] = useState(null);

  useEffect(() => {

    const getGuestSessionId = async () => {
      const sessionId = await fetchGuestSession();
      setGuestSessionId(sessionId);
    };

    getGuestSessionId();
  }, []);

  useEffect(() => {
    const loadDefaultMovies = async () => {
      setLoading(true);
      try {
        const [moviesData] = await Promise.all([fetchMovies()]);
        setMovies(moviesData.results);
        setTotalResults(moviesData.total_results);
      } catch (error) {
        console.error("Ошибка загрузки:", error);
      } finally {
        setLoading(false);
      }
    };

    loadDefaultMovies();
  }, []);

  useEffect(() => {
    const loadSearchMovies = async () => {
      setLoading(true);
      try {
        const response = await fetchMovies(searchQuery, currentPage);
  
        if (Array.isArray(response)) {
          setMovies(response);
          setTotalResults(response.length); 
        } else {
          const { results, total_results } = response;
          setMovies(results);
          setTotalResults(total_results); 
        }
      } catch (error) {
        console.error("Ошибка поиска:", error);
      } finally {
        setLoading(false);
      }
    };
  
    if (activeTab === "search") {
      loadSearchMovies();
    }
  }, [searchQuery, currentPage, activeTab]);

  const debouncedSearch = useMemo(
    () => debounce((value) => {
      setSearchQuery(value);
      setCurrentPage(1);
    }, 500),
    []
  );

  const handleRate = (movie, value) => {
    const updated = { ...ratedMovies, [movie.id]: { ...movie, rating: value } };
    setRatedMovies(updated);
    localStorage.setItem("ratedMovies", JSON.stringify(updated));
    voteForMovie(movie.id, value, guestSessionId);
  };

  const handleTabChange = (key) => {
    setActiveTab(key);
    setCurrentPage(1);
  };

  const getGenreNames = (ids) =>
    ids.map((id) => genres.find((g) => g.id === id)?.name).filter(Boolean);

  const formatDescription = (text) =>
    !text ? "" : text.length <= 100 ? text : text.substring(0, text.lastIndexOf(" ", 100)) + "...";

  const renderMovieCard = (movie) => {
    const ratingColor = getRatingColor(movie.vote_average);

    return (
      <Col key={movie.id} xs={24} sm={24} md={12} lg={12}>
        <Card hoverable>
          <div className="movie-card">
            <div className="rating-badge" 
            style={{ 
              backgroundColor: 'transparent', 
              border: `3px solid ${ratingColor}`,
              }}>
              {movie.vote_average?.toFixed(1)}
            </div>
            {movie.poster_path ? (
              <img
                alt={movie.title}
                src={`https://image.tmdb.org/t/p/w200${movie.poster_path}`}
                className="movie-image"
              />
            ) : (
              <div className="movie-placeholder">No image</div>
            )}
            <div className="movie-content">
              <h3>{movie.title}</h3>
              <div className="movie-date">
                {movie.release_date
                  ? format(new Date(movie.release_date), "MMMM d, yyyy")
                  : "Unknown release date"}
              </div>
              <div className="movie-tags">
                {getGenreNames(movie.genre_ids || []).map((name) => (
                  <span key={name}>{name}</span>
                ))}
              </div>
              <div className="movie-description">{formatDescription(movie.overview)}</div>
              <Rate
                count={10}
                allowHalf
                value={ratedMovies[movie.id]?.rating || 0}
                onChange={(value) => handleRate(movie, value)}
              />
            </div>
          </div>
        </Card>
      </Col>
    );
  };

  const getRatingColor = (rating) => {
    if (rating <= 3) return "#E90000";
    if (rating <= 5) return "#E97E00";
    if (rating <= 7) return "#E9D100";
    return "#66E900";
  };

  const ratedList = Object.values(ratedMovies);
  const currentList = activeTab === "search"
  ? (movies || []).slice((currentPage - 1) * 6, currentPage * 6) 
  : (ratedList || []).slice((currentPage - 1) * 6, currentPage * 6);

  return (
    <div className="movie-page-wrapper" style={{ padding: 24 }}>
      <Tabs activeKey={activeTab} 
      onChange={handleTabChange} 
      centered
      items={[
        {label: 'Search', key: 'search'},
        {label: 'Rated', key: 'rated'},
      ]}
      />

      {activeTab === "search" && (
        <Input
          placeholder="Type to search..."
          onChange={(e) => debouncedSearch(e.target.value)}
          style={{ marginBottom: 24 }}
        />
      )}

      {loading ? (
        <Spin size="large" />
      ) : (
        <div className="movie-list-container">
          <Row gutter={[16, 16]} justify="center">
            {currentList.length > 0 ? (
              currentList.map(renderMovieCard)
            ) : (
              <div>Ничего не найдено.</div>
            )}
          </Row>
          <div style={{ display: "flex", justifyContent: "center", marginTop: 24 }}>
            <Pagination
              current={currentPage}
              total={activeTab === "search" ? totalResults : ratedList.length}
              pageSize={6}
              onChange={(page) => setCurrentPage(page)}
            />
          </div>
        </div>
      )}
    </div>
  );
};

export default MovieList;