const API_KEY = "f0abdcf7d95e9bbb5f5bbb9975a5435c";
const BASE_URL = "https://api.themoviedb.org/3";
const PROXY_URL = ""; // Если нужен прокси, укажите URL

export const ALL_GENRES = [
  { id: 28, name: "Action" },
  { id: 12, name: "Adventure" },
  { id: 16, name: "Animation" },
  { id: 35, name: "Comedy" },
  { id: 80, name: "Crime" },
  { id: 99, name: "Documentary" },
  { id: 18, name: "Drama" },
  { id: 10751, name: "Family" },
  { id: 14, name: "Fantasy" },
  { id: 36, name: "History" },
  { id: 27, name: "Horror" },
  { id: 10402, name: "Music" },
  { id: 9648, name: "Mystery" },
  { id: 10749, name: "Romance" },
  { id: 878, name: "Science Fiction" },
  { id: 10770, name: "TV Movie" },
  { id: 53, name: "Thriller" },
  { id: 10752, name: "War" },
  { id: 37, name: "Western" }
];

const fetchApi = async (endpoint, options = {}) => {
  try {
    const response = await fetch(`${PROXY_URL || BASE_URL}${endpoint}`, {
      headers: {
        "Content-Type": "application/json",
        ...options.headers, // Добавление дополнительных заголовков, если они есть
      },
      ...options,
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    return await response.json();
  } catch (error) {
    console.error(`API request failed: ${error}`);
    throw error; // Пробрасывание ошибки дальше
  }
};

// Получение жанров
export const fetchGenres = async () => {
  try {
    const response = await fetchApi(`/genre/movie/list?api_key=${API_KEY}`);
    return response.genres || ALL_GENRES;
  } catch (error) {
    console.log("Error fetching genres:", error);
    return ALL_GENRES;
  }
};

// Получение списка фильмов
export const fetchMovies = async (query = "") => {
  try {
    const endpoint = query
      ? `/search/movie?api_key=${API_KEY}&query=${encodeURIComponent(query)}`
      : `/movie/popular?api_key=${API_KEY}`;

    const response = await fetchApi(endpoint);
    return response.results || [];
  } catch (error) {
    console.error("Error fetching movies:", error);
    return [];
  }
};

// Оценка фильма
export const voteForMovie = async (movieId, rating, guestSessionId) => {
  try {
    const response = await fetchApi(
      `/movie/${movieId}/rating?api_key=${API_KEY}&guest_session_id=${guestSessionId}`,
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ value: rating }),
      }
    );
    return response;
  } catch (error) {
    console.error("Error submitting vote:", error);
  }
};

// Получение guest session ID
export const fetchGuestSession = async () => {
  try {
    const response = await fetchApi(
      "/authentication/guest_session/new?api_key=" + API_KEY
    );
    return response.guest_session_id;
  } catch (error) {
    console.error("Error fetching guest session:", error);
    return null; // Возвращаем null, если не удалось получить session ID
  }
};